body {
  margin: 0px;
  font-family: "Arial Rounded MT Bold";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #475351;
  text-align: left;
  background-color: #a4d0ba;
}

.navbar {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 1rem 1rem;
  margin-bottom: 20px;
}

.navbar-custom {
  background-color: #7cb1a5;
}

.navbar-custom .navbar-brand,
.navbar-custom .navbar-brand:focus {
  color: #fff;
  font-size: 2rem;
}
.navbar-custom .navbar-brand:hover {
  color: pink;
}

.navbar-custom .navbar-nav .nav-link {
  color: #fff;
  font-size: 1.4rem;
}
.navbar-custom .navbar-nav .nav-link:focus,
.navbar-custom .navbar-nav .nav-link:hover {
  color: pink;
}

.card {
  padding: 20px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-color: #dce4d8;
  background-clip: border-box;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
}

.rounded {
  border-radius: 0.7rem !important;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: 20px;
  margin-left: 20px;
  margin-bottom: 20px;
}

.img-responsive-bug {
  width: 100%;
  height: 100px;
}

.img-responsive-fish {
  width: 100%;
  height: 100px;
}

.img-responsive-villager {
  width: 100%;
  height: 400px;
}

.img-responsive-fossil {
  width: 100%;
  height: 200px;
}

.full-width-div {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}

.modal {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 100%;
}

.modal-main {
  position: fixed;
  width: 80%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-content {
  max-height: 92vh;
  overflow-y: auto;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
